"use strict";/**
 * 
 * Helper class for working with local storage
 * 
 * returns standard CRUD actions with local storage
 * 
 */(function(window,$){var lStorage=function(){var localStorage=window.localStorage;var ls={};ls.hasData=function(key){return!!localStorage[key]&&!!localStorage[key].length;};ls.get=function(key){if(!this.hasData(key)){return false;}var data=localStorage[key];try{return JSON.parse(data);}catch(e){return data;}};ls.set=function(key,value){try{localStorage.setItem(key,JSON.stringify(value));}catch(e){localStorage.setItem(key,value);}};ls.extend=function(key,value){if(this.hasData(key)){var _value=this.get(key);$.extend(_value,JSON.parse(JSON.stringify(value)));this.set(key,_value);}else{this.set(key,value);}};ls.remove=function(key){localStorage.removeItem(key);};return ls;}();})(undefined,jQuery);
'use strict';/**
 *
 * Transformer
 * 
 * Toggles the transform-active class for elements that have
 * transform animation events
 */(function(window,$){var Transformer=function Transformer(el){//cache variables
this.$el=$(el);this.init();};Transformer.prototype.init=function(){//add event handlers
this.$el.on('click',function(){$(this).toggleClass('transform-active');});};$(document).ready(function(){$('.has-transform').each(function(){new Transformer(this);});});})(undefined,jQuery);
'use strict';/*
These functions make sure WordPress
and Foundation play nice together.
*/jQuery(document).ready(function(){// Remove empty P tags created by WP inside of Accordion and Orbit
jQuery('.accordion p:empty, .orbit p:empty').remove();// Adds Flex Video to YouTube and Vimeo Embeds
jQuery('iframe[src*="youtube.com"], iframe[src*="vimeo.com"]').each(function(){if(jQuery(this).innerWidth()/jQuery(this).innerHeight()>1.5){jQuery(this).wrap("<div class='widescreen responsive-embed'/>");}else{jQuery(this).wrap("<div class='responsive-embed'/>");}});});